import { Component } from "react";
import { Alert, Button, Card, Col, Container, Image, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import { baseUrl, FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { generateRandomArray, GET_NETWORK_ICON, GET_NETWORK_NAME } from "../../../../../utils";
import { getCustomNetwork } from "../../../../../actions/appsMoreAction";
import withRouter from "../../../../routes/WithRouter";

class ManualHead extends Component{
    state={
        networkList:[{id:18}, {id:28}],
        loader:false
    }
    
    // componentDidMount(){
    //     getCustomNetwork().then(response => {
    //         const {responseData} = response.data
    //         if(responseData?.length > 0) this.setState({networkList:responseData, loader:false})
    //     })
    // }

    render(){
        const {loader, networkList} = this.state
        const { onBoarding, setNetwork, navigate } = this.props
        return <div className='d-flex flex-column flex-column-fluid w-100 py-5 manual_upload_'>
            {!onBoarding ? <div className="subheader">
                <Container fluid>
                    <div className='d-flex align-items-center'>
                        <Link to={`/content/addfeed/${localStorage.getItem('wallId')}`} className="btn btn-icon bg-gray-100 btn-sm me-2"><FontAwesomeIcon icon={'angle-left'} iconClass={'me-0'}/></Link>
                        <h1 className="me-2 h4 my-3">Upload Feed</h1>
                    </div>
                </Container>
            </div> : null}
            <Container fluid>
                <Card className={`${onBoarding?'shadow-none border-0':''}`}>
                    <Card.Body className='f-center flex-column manual_upload_card'>
                        <h3 className='text-center mb-3'>Manual Uploads</h3>
                        <p className='text-center mb-8'>Upload content directly to the feed via device or by adding a post URL from Instagram or TikTok </p>
                        <div className='f-center flex-wrap w-100 flex-column'>
                            {!loader ? 
                            <Row className='w-100' style={{maxWidth:600}}>
                                <Col>
                                    <Button onClick={() => onBoarding ? setNetwork(0) : navigate(`/manual-upload/feed?network=0`)} className='btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4'>
                                        <p className='display-2 mb-3'>
                                            <FontAwesomeIcon type={'light'} icon={'laptop'} iconClass={'me-0'}/>
                                        </p>
                                        <p className='mb-0 fs-10 fw-medium'>My Device</p>
                                    </Button>
                                </Col>

                                {networkList.length > 0 ? networkList.map(network =><Col>
                                    <Button onClick={() => onBoarding ? setNetwork(network.id) : navigate(`/manual-upload/feed?network=${network.id}`)} className='btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4'>
                                        <p className='display-2 mb-3'>
                                            <Image src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg`)} height={36} width={36} fluid/>
                                        </p>
                                        <p className='mb-0 fs-10 fw-medium'>{GET_NETWORK_NAME(network.id)}</p>
                                    </Button>
                                </Col>) : null}
                            </Row> 
                            :
                            <Row className='w-100' style={{maxWidth:600}}>
                            {generateRandomArray(3).map((_, index) => <Col>
                                <div key={index} className="btn btn-outline-secondary w-100 rounded-1 f-center flex-column p-7 mb-4 d-flex">
                                    <div className="sk_rect h-40px w-40px mb-3"></div>
                                    <div className="sk_line h-10px mb-1 w-75"></div>
                                </div>
                                </Col>)}
                            </Row>
                            }
                            {onBoarding ? 
                                <div className="f-center">
                                    <Button variant='link' className="text-decoration-underline" onClick={() => this.props.onBack('c_n')}>
                                        Choose another Network
                                    </Button>
                                </div>: null}
                        </div>
                    </Card.Body>
                </Card>
            </Container>
        </div>
    }
}

export default withRouter(ManualHead)